@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
* {
  box-sizing: border-box;
}

body {
  background: #fff;
}

.bg-container {
  margin: 96px auto;
  max-width: 1400px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  gap: 95px;
}
.bg-container .sm-container {
  max-width: 505px;
  max-height: 330px;
}
.bg-container .image-hero img {
  width: 100%;
  height: 100%;
}
.bg-container .content {
  display: flex;
  flex-direction: column;
  gap: 32px;
}
.bg-container .content .section {
  display: grid;
  gap: 24px;
  grid-template-rows: repeat(2, 1fr);
}
.bg-container .content .section .title {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  align-self: flex-end;
}
.bg-container .content .section .text {
  max-width: 380px;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
}
.bg-container .content > button {
  background: #e4c1ae;
  width: 128px;
  height: 48px;
  border-radius: 32px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #fff;
  border: 2px solid transparent;
}
.bg-container .content > button:hover {
  cursor: pointer;
  border-color: rgb(119, 238, 212);
}

.layout {
  left: 0;
  top: 0;
  position: absolute;
  background: rgba(12, 12, 12, 0.5);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.layout .alert-container {
  background: #fff;
  width: 920px;
  height: 210px;
}